* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-image: linear-gradient(#000000aa, #000000aa),
    url("https://media.giphy.com/media/3o6gE8cWI3n5mCvQZ2/giphy.gif");
  background-size: auto 65vh;
  background-position: center 5vh;
  background-repeat: no-repeat;
  height: 90vh;
  background-color: black;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

@font-face {
  font-family: "page-title";
  src: url("./assets/fonts/page-title.otf");
}

@font-face {
  font-family: "nav";
  src: url("./assets/fonts/nav.ttf");
}

@font-face {
  font-family: "card-title";
  src: url("./assets/fonts/card-title.ttf");
}

@font-face {
  font-family: "card-body";
  src: url("./assets/fonts/card-body.ttf");
}

a {
  text-decoration: none;
  color: inherit;
}
